.chatbot {
    position: fixed;
    bottom: 40px;
    right: 10px;
    z-index: 1000;
    max-width: 100%;
    max-height: 100%;
  }
  
.chat-icon {
    background-color: #be7124;
    color: white;
    border: none;
    border-radius: 50%;
    width: 60px;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 24px;
    cursor: pointer;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  }
  
.chat-window {
    width: 400px;
    height: 700px;
    border: 1px solid #ccc;
    border-radius: 10px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
    background-color: white;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    transform: scale(0);
    transition: transform 0.3s ease-in-out;
    max-width: 100%;
    max-height: 100%;
  }

.chat-icon-container {
    display: flex;
    align-items: center;
  }  
  
.chat-window.open {
    transform: scale(1);
  }
  
.chat-header {
    background-color: #be7124;
    color: white;
    padding: 10px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.chat-icon-text {
    background-color: #f0f0f0;
    color: black; 
    font-size: 16px; 
    font-weight: bold;
    border-radius: 10px; 
    width: 200px;
    padding-top: 15px;
    height: 50px;
    position: absolute;
    right: 430px;
  } 

.chat-icon-text::before {
    content: '';
    position: absolute;
    top: 15px; 
    right: -10px; 
    width: 0;
    height: 0;
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    border-bottom: 10px solid #f0f0f0;
}  
  
.header-content {
    display: flex;
    align-items: center;
  }
  
.header-content span {
    padding: 10px;
    font-weight: bold;
    font-size: x-large;
  }
  
.chat-header img {
    border-radius: 50%;
    width: 40px;
    height: 40px;
    margin-right: 10px;
  }
  
.close-button{
    cursor: pointer;
    width: 25px;
    height: 25px;
    margin-left: 200px;
  }
  
.chat-body {
    flex: 1;
    display: flex;
    flex-direction: column;
    overflow-y: auto;
    padding-bottom: 20px;
  }
  
.messages {
    flex: 1;
    padding: 10px;
    overflow-y: auto;
    padding-bottom: 30px;
    scroll-behavior: smooth;
  }
  
.message {
    display: flex;
    align-items: flex-start;
    margin: 10px 0;
  }
  
.message.bot-message {
    justify-content: flex-start;
  }
  
.message.user-message {
    justify-content: flex-end;
  }
  
.avatar-container {
    margin-right: 10px;
  }
  
.avatar {
    border-radius: 50%;
    width: 30px;
    height: 30px;
  }
  
.message-text {
    background-color: #f0f0f0;
    font-weight: bold;
    padding: 10px;
    border-radius: 10px;
    max-width: 70%;
    text-align: left;
    word-wrap: break-word;
    overflow-wrap: break-word;
    font-size: 16px;
  }
  
.input-container {
    display: flex;
    border-top: 1px solid #ccc;
    padding: 10px;
    position: absolute;
    bottom: 0;
    width: 95%;
    box-sizing: border-box;
  }
  
.file-input-container {
    display: flex;
    align-items: center;
    margin-top: -7px;
    padding: auto;
    width: 95%;
    border-radius: 5px;
}

.input-container input {
    flex: 1;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 16px;
  }
  
.input-container button {
    background-color: #be7124;
    color: white;
    border: none;
    border-radius: 5px;
    padding: 10px;
    margin-left: 10px;
    cursor: pointer;
  }

.typing-indicator .message-text {
  background-color: #be7124;
  font-weight: bold;
  padding: 10px;
  border-radius: 10px;
  max-width: 70%;
  text-align: left;
  display: flex;
  align-items: center;
  font-size: 16px;
}

.typing {
  display: flex;
  gap: 3px; /* Space between the dots */
}

.typing span {
  width: 6px;
  height: 6px;
  background-color: #ffffff;
  border-radius: 50%;
  display: inline-block;
  animation: blink 1.5s infinite;
}

.typing span:nth-child(1) {
  animation-delay: 0s;
}

.typing span:nth-child(2) {
  animation-delay: 0.3s;
}

.typing span:nth-child(3) {
  animation-delay: 0.6s;
}

@keyframes blink {
  0%, 80%, 100% {
    opacity: 0;
  }
  40% {
    opacity: 1;
  }
}

/* Media Queries for Responsiveness */
  @media (max-width: 768px) {
    .chat-window {
        width: 90%;
        height: 80%;
        max-width: 90%;
        max-height: 80%;
        border: 1px solid #ccc;
        border-radius: 10px;
        box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
    }
    .chat-header img {
        width: 30px;
        height: 30px;
    }
    .header-content span {
        font-size: large;
    }
    .close-button {
        width: 20px;
        height: 20px;
    }
    .message-text {
        font-size: 14px;
    }
    .input-container {
        padding: 5px;
    }
    .input-container input {
        padding: 5px;
    }
    .input-container button {
        padding: 5px;
    }
    .chat-icon-text {
      right: 140px;
    }
}

@media (max-width: 480px) {
    .chat-window {
        width: 100%;
        height: 100%;
        border-radius: 0;
        border: 1px solid #ccc;
        border-radius: 10px;
        box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
        margin-left: 20px;
    }

    .chat-icon{
      margin-left: 210px;
    }
    .chat-header {
        padding: 5px;
    }
    .header-content span {
        font-size: medium;
    }
    .close-button{
      width: 20px;
      height: 20px;
      margin-left: 180px;
    }
    .message-text {
        font-size: 12px;
    }
    .input-container {
        padding: 5px;
    }
    .input-container input {
        padding: 5px;
    }
    .input-container button {
        padding: 5px;
    }
    .chat-icon-text {
      right: 155px;
    }
}